nav {
  display: flex;
  flex-direction: column;
}

nav button {
  width: 100%;
  height: 3em;
  margin: 1em 0;
  font-size: 1em;
  display: flex;
  background-color: #994D64;
}

nav button img {
  width: 1.5em;
  margin: 0 0.5em;
}

nav button:hover,
.active {
  cursor: pointer;
  background-color: rgb(95, 48, 62);
  color: #FBB15E;
}